import Vue from 'vue'
import Router from 'vue-router'

import Tab from '@/page/front/Tab'//tab
import about from '@/page/front/about';//
import Home from '@/page/front/Home';//
import Catagory from '@/page/front/Catagory';//
import artical from '@/page/front/artical';//
import CataArtical from '@/page/front/catagory-artical';//
import fontLab from '@/page/front/lab';//
//import labDetail from '@/components/demo-detail';


import back from "@/page/back/back";//
import backArtical from "@/page/back/back-artical";//
import backUser from '@/page/back/back-user'//
import backCategory from '@/page/back/back-catagory';//
import backMusic from '@/page/back/back-music';//
import backLab from '@/page/back//back-laboratory';//








Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'Hello',
      component: Tab,
      redirect: '/index/home',
      children:[
        {
          path:'home',
          component:Home,
          name:'home'
        },
        {
          path:'artical',
          component:artical,
          name:'artical'
        },
        {
          path:'catagory',
          component:Catagory,
          name:'catagory'
        },
        {
          path:'cataArtical',
          component:CataArtical,
          name:'cataArtical'
        },
        {
          path:'about',
          component:about,
          name:'about'
        },
        {
          path:'lab',
          component:fontLab,
          name:'lab'
        },
        /*{
          path:'labDetail',
          component:labDetail,
          name:'labDetail'
        }*/
      ]
    },
    {
      path: '/back',
      redirect: '/back/user',
      name: 'back',
      component: back,
      children: [
        {
          path: 'user',
          component: backUser,
          name:'user'
        },
        {
          path: 'category',
          component: backCategory,
          name:'category'
        },
        {
          path: 'artical',
          component: backArtical,
          name:'artical'
        },
        {
          path: 'music',
          component: backMusic,
        },
        {
          path: 'laboratory',
          component: backLab,
          name:'laboratory'
        },

      ]
    },
    {
      path: '*',
      redirect: '/index'
    }
  ]
})



